  methods: {
    generateWords() {
      const currentList = this[this.selectedList]; 
      
      const shuffledWords = _.shuffle(currentList[0]); // Reshuffle the list
      this.randomWords = shuffledWords.slice(0, 5); // Displays the first 5 words form the reshuffled list
