    },
    generateGrammar() {
      const grammarList = this[this.selectedList][1];

      const selectedGrammar = []; // Create list of all grammar points in English
      for (var key in grammarList) {
        selectedGrammar.push(key);
      }

      const shuffledGrammar = _.shuffle(selectedGrammar); // Create the list of shuffled grammar points
      
      const english = shuffledGrammar[0];
      const kana = grammarList[english][0];
      const link = grammarList[english][1];

      this.currentGrammar = {
        kana: kana,
        english: english,
        link: link
      }
    },
    generator() {
      this.generateWords();
      this.generateGrammar();
    }
